@font-face {
  font-family: Roboto;
  src: url(./components/assets/Roboto/Roboto-Regular.ttf);
}
body{
  font-family: Roboto!important;
}
.MuiTableSortLabel-root{
  font-size: 20px!important;
  font-weight: 600!important;
}
.MuiTableCell-head{
  padding: 0px 5px 5px 5px!important;
}
.table .MuiPaper-rounded{
  padding: 5px 20px 0px 20px!important;
  border-radius: 10px!important;
  border: 1px solid #CACACA!important;
  box-shadow: none!important;
}
.MuiAutocomplete-popper{
  display: none!important;
}
.MuiDialog-paper{
  border-radius: 8px!important;
  border-top:3px solid #5C79E0!important;
  padding: 0px!important;
}
.d-none{
  display: none!important;
}
.MuiDialog-paperWidthSm{
  width: 320px!important;
}
.btnContained{
  background-color: #5C79E0!important;
  text-transform: capitalize!important;
  font-weight: 600!important;
}
tbody .MuiTableCell-root{
font-size: 18px!important;
font-family: Roboto!important;
padding: 22px 10px!important;
}
.MuiDrawer-paperAnchorDockedLeft{
  border-right:0px!important ;
}
.cursor{
  cursor: pointer;
}
input::placeholder{
  color:#5C79E0!important;
  opacity: 1!important;
  font-size: 19px!important;
}
.inputHeight input::placeholder{
  color:#CACACA!important;
}
.nav-input{
  width: 380px;
}
.nav-input .MuiOutlinedInput-inputMarginDense{
  padding-top: 16.5px!important;
  padding-bottom: 16.5px!important;
  font-size: 19px!important;
}
.user{
  padding: 10px;
  width: 20px;
  border-radius: 50%;
  margin-left: 30px;
  font-size: 20px;
  background-color: #5C79E0;
}
.MuiPaper-rounded .MuiToolbar-root{
  display: none!important;
}
.inputHeight .MuiOutlinedInput-input{
  padding: 16.5px 14px!important;
  font-size: 22px!important;
}
.autocomplete .MuiOutlinedInput-input{
  padding: 9.5px 14px!important;
  font-size: 22px!important;
}
.autocomplete input::placeholder{
  color:#616161!important;
}
.textArea .MuiOutlinedInput-input{
  font-size: 22px!important;
}
.upload-btn{
  color:"#5C79E0!important";
  text-transform:none!important;
  font-weight: 600!important;
  margin-bottom: 30px!important;
  border: 2px solid #5C79E0!important;
}
.MuiInputLabel-outlined{
  transform: translate(22px, 20px) scale(1)!important;
  font-size: 19px!important;
}
.MuiChip-deletable{
  background-color: #5C79E0!important;
  color:#fff!important;
  font-size: 18px!important;
  text-transform: uppercase;
}
.MuiAppBar-positionFixed{
  position: relative!important;
}
.MuiInputLabel-outlined.MuiInputLabel-shrink{
  transform: translate(24px, -6px) scale(0.57)!important;
  background-color: #fff!important;
  padding: 0px 10px!important;
}
.colorWhite{
  color:#fff!important;
}
.MuiInputLabel-shrink legend > span{
  max-width: 200px!important;
  padding-right: 20px!important;
}
.inputHeight fieldset legend,.textArea fieldset legend{
max-width: 0px!important;
}
.iconButton{
  float: right;
  padding-left: 0px!important;
  padding-right: 0px!important;
}

.MuiDropzoneArea-text{
  font-family: Roboto!important;
  font-size: 18px!important;
  color:#616161!important
}
.MuiDropzoneArea-root{
  background-color: #F9F9FE!important;
  border: 3px dashed #B5BBCD!important;
}
.MuiDropzonePreviewList-image{
  object-fit: cover!important;
}
.MuiDropzoneSnackbar-successAlert, .MuiDropzoneSnackbar-infoAlert{
 margin-left: 100px!important;
}
.MuiDropzoneArea-icon{
  display: none!important;
}
.react-tagsinput input {
  font-size: 19px!important;
  margin: 0px!important;
}
.react-tagsinput{
  border-radius: 8px!important;
  border-width: 2px!important;
  /* height: 40px!important; */
    padding-top: 12px!important;
    padding-bottom: 12px!important;
  margin-bottom: 59px!important;
}
.react-tagsinput--focused{
  border:2px solid #5C79E0!important;
}
.react-tagsinput-tag{
  border:1px solid #5C79E0!important;
  background-color: #5C79E0!important;
  color:#fff!important;
  font-size: 19px!important;
  font-family: Roboto!important;
  font-weight: 400!important;
  padding: 5px!important;
  margin-top: -5px!important;
  border-radius: 20px!important;
  padding: 4px 10px!important;
  margin-right: 3px!important;
  margin-bottom: 0px!important;
}
.react-tagsinput-remove{
  margin-left: 10px!important;
}
@media (max-width: 1920px){
  .MuiTableSortLabel-root{
    font-size: 14px!important;
    font-weight: 600!important;
  }
  .fonttable{
    font-size: 14px!important;
    margin-top: 28px!important;
  }
  .fonttable2{
    font-size: 14px!important;
    margin-top: 4px!important;
  }
  .fonttable div{
    font-size: 10px!important;
  }
  tbody .MuiTableCell-root{
    font-size: 13px!important;
    }
    input::placeholder{
      font-size: 14px!important;
    }
    .nav-input .MuiOutlinedInput-inputMarginDense{
      /* padding-top: 8.5px!important;
      padding-bottom: 12.5px!important; */
      font-size: 14px!important;
    }
    .MuiChip-deletable{
      font-size: 12px!important;      
    }
    textArea .MuiOutlinedInput-input{
      font-size: 14px!important;
    }
    .MuiPaper-rounded{
      padding-top: 0px!important;
    }
    .MuiInputLabel-outlined{
      transform: translate(22px, 15px) scale(1)!important;
      font-size: 14px!important;
    }
    .inputHeight .MuiOutlinedInput-input{
      padding: 14.5px 14px!important;
      font-size: 14px!important;
    }
    .autocomplete .MuiOutlinedInput-input{
      padding: 6px 14px!important;
      font-size: 14px!important;
    }
}
.react-tagsinput{
  border-radius: 8px!important;
    border-width: 1px!important;
    
    padding-top: 9px!important;
    
    
    padding-bottom: 9px!important;
    margin-bottom: 30px!important;
}
.react-tagsinput input {
  font-size: 14px!important;
  margin: 0px!important;
}
.react-tagsinput-tag{
  border:1px solid #5C79E0!important;
  background-color: #5C79E0!important;
  color:#fff!important;
  font-size: 14px!important;
  font-family: Roboto!important;
  font-weight: 400!important;
  padding: 5px!important;
  margin-top: -5px!important;
  border-radius: 20px!important;
  padding: 4px 10px!important;
  margin-right: 3px!important;
  margin-bottom: 0px!important;
}
@media (min-width: 600px){
  .containernav{
    max-width: 550px!important;
    }
    .nav-input{
      max-width: 300px!important;
    }
}
@media (min-width: 960px){
  .containernav{
  max-width: 900px!important;
  }
}
@media (min-width: 1280px){
  .containernav{
    max-width: 1100px!important;
  }
  .container-box{
    max-width: 1180px!important;
  }
}
@media (min-width: 1920px){

.container-box{
  max-width: 1480px!important;
}
