  max-width: 1322px!important;

}
}

@media (max-width: 1920px){
  .MuiChip-deletable{
    height: 25px!important;
    margin:0px 3px 0px 3px!important;
  }
}
@media (max-width: 1280px){
  .user{
  padding: 4px!important;
  width: 10px!important;
  border-radius: 50%!important;
